<template>
	<view class="body">
		<!-- 头部模块 -->
		<view class="head">
			<view class="word" @click="toNewWord">
				<img src="../../static/langStatic/wordBook.png">
				<text>单词本</text>
			</view>
			<view class="head_title">完型，2004</view>
			<view class="difficulty">
				<view>难度</view>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#ffd75c" size="28rpx"></u-icon>
				<u-icon name="star-fill" color="#e3e3e3" size="28rpx"></u-icon>
			</view>
		</view>
		<!-- 句子 -->
		<view class="sentence" v-html="data.subject"></view>
		<!-- 单元 -->
		<view class="title">
			<view class="number">{{data.index}}</view>
			<view class="text">{{data.title}}</view>
		</view>
		<!-- 答案 -->
		<view class="answerList" v-if="isSee==false">
			<view class="answer" v-for="(item,index) in data.option" :key="index" @click="confirm(index)">
				<view v-if="index==0" :class="{serial:true, checked:item.isChecd} ">A</view>
				<view v-else-if="index==1" :class="{serial:true, checked:item.isChecd} ">B</view>
				<view v-else-if="index==2" :class="{serial:true, checked:item.isChecd} ">C</view>
				<view v-else-if="index==3" :class="{serial:true, checked:item.isChecd} ">D</view>
				<view v-else-if="index==4" :class="{serial:true, checked:item.isChecd} ">E</view>
				<view v-else-if="index==5" :class="{serial:true, checked:item.isChecd} ">F</view>
				<view v-else-if="index==6" :class="{serial:true, checked:item.isChecd} ">G</view>
				<view v-else-if="index==7" :class="{serial:true, checked:item.isChecd} ">H</view>
				<view class="text">{{item.option}}</view>
			</view>
			<button @click="seeResolution">查看解析</button>
		</view>
		<!-- 答案 -->
		<view class="answerList" v-else>
			<view class="answer" v-for="(item,index) in data.option_answer" :key="index">
				<view v-if="index==0" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">A</view>
				<view v-else-if="index==1" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">B</view>
				<view v-else-if="index==2" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">C</view>
				<view v-else-if="index==3" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">D</view>
				<view v-else-if="index==4" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">E</view>
				<view v-else-if="index==5" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">F</view>
				<view v-else-if="index==6" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">G</view>
				<view v-else-if="index==7" :class="{serial:true, trueAnswer:item.flag=='true' || item.answer=='true', falseAnswer:item.flag=='false' && item.result==true} ">H</view>
				<view class="text">{{item.option}}</view>
			</view>
			<button>查看解析</button>
		</view>
		<!-- 答案解析 -->
		<view v-show="isSee" class="answerAnalysis">
			<view class="title">答案解析</view>
			<view class="content" v-html="data.analysis"></view>
		</view>
		<!-- 视频 -->
		<view v-if="data.video" v-show="isSee" class="answerAnalysis">
			<view class="title">教你拆解法的短视频</view>
			<video :src="data.video" id="videos" class="videos" controls @click="closeSpeedList" object-fit="cover">
				<cover-view class="video-control">
					<cover-view class="multi rate" @tap="showSwitchRate">X {{currentRate}}</cover-view>
				</cover-view>
				<cover-view class="multi-list rate" :class="{ active: rateShow }">
					<cover-view
						v-for="(item, index) in ['0.5', '1.0', '1.5', '2.0']"
						:key="index"
						class="multi-item rate"
						:data-rate="item"
						@tap="switchRate"
						:class="{ active: item == currentRate }"
					>
						{{ item }}
					</cover-view>
				</cover-view>
			</video>
		</view>
		<!-- 下一步 -->
		<button @click="toNext">下一步</button>
	</view>
</template>

<script>
	import store from '@/store/index.js'; //引入store
	import { subject, checkAnswer } from '@/common/http.api.js';
	export default{
		data() {
			return {
				rateShow: false, // 倍速浮层
				currentRate: 1.0, //默认1倍速
				radiolist1:[],
				token:'',
				isSee:false,
				answer:'',
				data:{}
			}
		},
		onLoad() {
			this.videoContext = uni.createVideoContext('videos')
			this.token = store.state.vuex_token 
			let id = store.state.subject_id //大题ID
			subject({token:this.token,subject_id:id,order:-1}).then(res=>{
				console.log(res)
				if(res.data.data.option_answer){
					this.isSee = true
				}
				this.data = res.data.data
				console.log(this.data)
			}).catch(err=>{
				console.log(err)
			})
		},
		methods: {
			// 关闭倍速列表
			closeSpeedList(){
				console.log(this.videoList)
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
			},
			// 显示倍速浮层
			showSwitchRate(rate) {
				if(this.videoList==true){
					this.rateShow = false;
					this.videoList = false;
				}
				let that = this;
				console.log('rateShow');
				that.rateShow = true;
				setTimeout(()=>{
					this.videoList = true
				},500)
			},
			// 切换倍速
			switchRate(e) {
				let that = this;
				let rate = Number(e.currentTarget.dataset.rate);
				that.currentRate = rate;
				that.rateShow = false;
				this.videoContext.playbackRate(rate);
				console.log(rate)
			},
			
			// 选择答案
			confirm(e) {
				console.log(e)
				for(let i in this.data.option){
					this.data.option[i].isChecd = false
				}
				this.data.option[e].isChecd = true
				console.log(this.data.option)
				console.log(this.data.option[e].isChecd)
				this.answer = this.data.option[e].option
			},
			// 查看解析
			seeResolution() {
				if(this.answer){
					this.isSee = true
					let answers = []
					let id = store.state.subject_id //大题ID
					this.data.option.map((item)=>{
						if(item.isChecd==true){
							return answers.push({answer:item.answer, option:item.option, result:true}) 
						}else{
							answers.push({answer:item.answer, option:item.option, result:null}) 
						}
					})
					let answer = JSON.stringify(answers)
					console.log("-----")
					console.log(answers)
					console.log(answer)
					let params = {
						answer: answer,
						answer_type:this.data.type,
						is_end:this.data.is_end,
						option_id:this.data.id,
						subject_id:id,
						token:this.token
					}
					checkAnswer({...params}).then(res=>{
						console.log(res)
						let id = store.state.subject_id //大题ID
						subject({token:this.token,subject_id:id,order:-1}).then(res=>{
							console.log(res)
							this.data = res.data.data
							console.log(this.data)
						}).catch(err=>{
							console.log(err)
						})
					}).catch(err=>{
						console.log(err)
					})
				}else{
					this.$u.toast("先尝试一下吧~")
				}
				
			},
			// 下一步
			toNext() {
				if(this.isSee==true){
					uni.navigateTo({
						url: '/pages/index/learnThree'
					})
				}else{
					this.$u.toast("做完再走~")
				}
			},
			// 去单词本
			toNewWord() {
				uni.navigateTo({
					url: '/pages/index/newWordBook'
				})
			}
		}
	}
</script>

<style lang="scss">
	.body{
		padding-bottom: 40rpx;
		button{
			background: linear-gradient(to right, #ffd23f, #fca51d);
			color: #fff;
			font-size: 32rpx;
			line-height: 88rpx;
			border-radius: 100rpx;
			width: 80%;
		}
		.head{
			padding: 0 20rpx;
			display: flex;
			justify-content: space-between;
			align-items: center;
			.word{
				width: 150rpx;
				position: relative;
				background-color: #fff4c7;
				color: #fcaa20;
				text-align: center;
				font-size: 28rpx;
				line-height: 70rpx;
				border-radius: 100rpx;
				padding-left: 30rpx;
				img{
					position: absolute;
					left: 26rpx;
					top: 16rpx;
					width: 30rpx;
					height: 40rpx;
				}
			}
			.difficulty{
				display: flex;
				view{
					color: #b2b2b2;
					font-size: 28rpx;
					margin-right: 10rpx;
				}
			}
		}
		.sentence{
			margin: 40rpx 30rpx;
			line-height: 68rpx;
		}
		.title{
			margin: 40rpx 30rpx;
			display: flex;
			margin-bottom: 30rpx;
			.number{
				background-color: #797979;
				color: #fff;
				width: 36rpx;
				line-height: 36rpx;
				height: 36rpx;
				text-align: center;
				margin-right: 20rpx;
			}
			.text{
				line-height: 36rpx;
				font-size: 30rpx;
				font-weight: 500;
				letter-spacing: 2rpx;
			}
		}
		.answerList{
			margin: 40rpx 30rpx;
			.answer{
				margin: 30rpx 0;
				display: flex;
				.serial{
					margin-right: 20rpx;
					border: 1px solid #cbcbcb;
					border-radius: 50%;
					color: #cbcbcb;
					width: 70rpx;
					line-height: 70rpx;
					text-align: center;
				}
				.checked{
					border: 1px solid #50d891;
					color: #50d891;
				}
				.trueAnswer{
					border: 1px solid #50d891;
					background-color: #50d891;
					color: #fff;
				}
				.falseAnswer{
					border: 1px solid #ff6227;
					background-color: #ff6227;
					color: #fff;
				}
				.text{
					line-height: 70rpx;
				}
			}
			button{
				padding: 0;
				color: #ffd942;
				width: 150rpx;
				border: 1px solid #ffd942;
				border-radius: 12rpx;
				font-size: 28rpx;
				line-height: 60rpx;
				background-color: transparent;
				background: none;
				margin-bottom: 40rpx;
			}
		}
		.answerAnalysis{
			margin: 30rpx 40rpx;
			.title{
				padding-left: 30rpx;
				background-image: url(../../static/point.png);
				background-repeat: no-repeat;
				background-size: 20rpx;
				margin: 0;
				font-size: 28rpx;
				color: #828282;
				background-position: left center; 
			}
			.content{
				margin: 30rpx 0;
			}
			video{
				width: 98%;
				margin: 30rpx 1%;
				height: 370rpx;
				border-radius: 10rpx;
			}
		}
		
		/* 视频CSS开始 */
		.video-control {
		    background-color: transparent;
		    height: 90rpx;
			border-top-left-radius: 20rpx;
			border-top-right-radius: 20rpx;
		    position: absolute;
		    top: 0;
		    right: 0;
		    width: 200rpx;
		    z-index: 1;
		}
		.video-wrap {
		    position: relative;
		}
		.multi-list.full-screen.vertical {
		    height: 100vh !important;
		    padding: 8vh 0;
		}
		.multi-list.full-screen.horizontal {
		    height: 100vw !important;
		    padding: 8vw 0;
		}
		.multi {
			text-align: center;
		    position: absolute;
		    right: 30rpx;
		    top: 50%;
		    transform: translateY(-50%);
		    z-index: 1;
		    width: 100rpx;
		    color: #fff;
		    padding: 10rpx 0;
		    text-align: center;
		    transition: color ease 0.3s;
		}
		.multi.rate {
		    right: 30rpx;
		}
		.multi-list {
		    position: absolute;
		    height: 100%;
		    width: 0;
		    background-color: rgba(0, 0, 0, 0.65);
		    top: 0;
		    right: 0;
		    transition: width 0.3s ease;
		    z-index: 1;
		    box-sizing: border-box;
		    padding: 50rpx 0;
		}
		.multi-list.rate {
		    padding: 25rpx 0;
		}
		.multi-list.active {
		    width: 300rpx;
		}
		.multi-item {
		    text-align: center;
		    color: #fff;
		    line-height: 80rpx;
		    transition: color ease 0.3s;
		}
		.multi-item.rate {
		    line-height: 70rpx;
		}
		/* 视频CSS结束 */
	}
</style>